import React, { Component } from "react";
import { Container, Typography } from "@material-ui/core";
class PrivacyPolicy extends Component {
  state = {};
  componentDidMount() {
    window.scrollTo(0, 0);
  }
  render() {
    return (
      <Container>
        <div>
          <h2 className="text-align-center p-10">
            <strong>Privacy Policy</strong>
          </h2>

          <Typography variant="body1" className="p-10">
            <strong>
              <em>Updated June 14, 2020</em>
            </strong>
          </Typography>

          <Typography variant="body1" className="p-10">
            We value the trust you place in us and recognize the importance of
            secure transactions and information privacy. This Privacy Policy
            describes how EGadgets Internet Private Limited and its affiliates
            (collectively “EGadgets, we, our, us”) collect, use, share or
            otherwise process your personal information through EGadgets website{" "}
            <a href="http://www.EGadgets.com">www.EGadgets.com</a>, its mobile
            application, and m-site (hereinafter referred to as the “Platform”).
          </Typography>
          <Typography variant="body1" className="p-10">
            While you may be able to browse certain sections of the Platform
            without registering with us, however, please note we do not offer
            any product/service under this Platform outside India. By visiting
            this Platform, providing your information or availing out
            product/service, you expressly agree to be bound by the terms and
            conditions of this Privacy Policy, the{" "}
            <a href="https://www.EGadgets.com/pages/terms">Terms of Use</a> and
            the applicable service/product terms and conditions, and agree to be
            governed by the laws of India including but not limited to the laws
            applicable to data protection and privacy. If you do not agree
            please do not use or access our Platform.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>1. Collection of Your Information</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            When you use our Platform, we collect and store your information
            which is provided by you from time to time. In general, you can
            browse the Platform without telling us who you are or revealing any
            personal information about yourself. Once you give us your personal
            information, you are not anonymous to us. Where possible, we
            indicate which fields are required and which fields are optional.
            You always have the option to not provide information by choosing
            not to use a particular service, product or feature on the Platform.
          </Typography>
          <Typography variant="body1" className="p-10">
            We may track your buying behaviour, preferences, and other
            information that you choose to provide on our Platform. We use this
            information to do internal research on our users' demographics,
            interests, and behaviour to better understand, protect and serve our
            users. This information is compiled and analysed on an aggregated
            basis. This information may include the URL that you just came from
            (whether this URL is on our Platform or not), which URL you next go
            to (whether this URL is on our Platform or not), your computer
            browser information, and your IP address.
          </Typography>
          <Typography variant="body1" className="p-10">
            We collect personal information (such as email address, delivery
            address, name, phone number, credit card/debit card and other
            payment instrument details) from you when you set up an account or
            transact with us. While you can browse some sections of our Platform
            without being a registered member, certain activities (such as
            placing an order or consuming our online content or services) do
            require registration. We do use your contact information to send you
            offers based on your previous orders and your interests.
          </Typography>
          <Typography variant="body1" className="p-10">
            If you choose to post messages on our message boards, chat rooms or
            other message areas or leave feedback or if you use voice commands
            to shop on the Platform, we will collect that information you
            provide to us. We retain this information as necessary to resolve
            disputes, provide customer support and troubleshoot problems as
            permitted by law.
          </Typography>
          <Typography variant="body1" className="p-10">
            If you send us personal correspondence, such as emails or letters,
            or if other users or third parties send us correspondence about your
            activities or postings on the Platform, we may collect such
            information into a file specific to you.
          </Typography>
          <Typography variant="body1" className="p-10">
            If you enrol into our SuperCoins Loyalty Program, we will collect
            and store your personal information such as name, contact number,
            email address, communication address, date of birth, gender, zip
            code, lifestyle information, demographic and work details which is
            provided by you to EGadgets or a third-party business partner that
            operates online/offline establishments or platforms where you can
            earn SuperCoins for purchase of goods and services, and redeem
            SuperCoins. We will also collect your information related to your
            transactions on EGadgets platform and such third-party business
            partner platforms. When such third-party business partner collects
            your personal information directly from you, you will be governed by
            their privacy policies. EGadgets shall not be responsible for the
            third-party business partner’s privacy practices or the content of
            their privacy policies, and we request you to read their privacy
            policies prior to disclosing any information.
          </Typography>
          <Typography variant="body1" className="p-10">
            EGadgets has onboarded certain third-party business partners on the
            Platform who specialise in the categories like travel ticket
            reservations, booking online movie tickets, paying online bills and
            more (Ultra-Partners). If you use the services of Ultra-Partners,
            you will be redirected to Ultra-Partners websites/applications and
            your entry to Ultra-Partners websites/applications will be based on
            your EGadgets login credentials after seeking your permissions to
            share the data further. EGadgets shall not be responsible for the
            Ultra-Partner’s privacy practices or the content of their privacy
            policies, and we request you to read their privacy policies prior to
            disclosing any information.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>
              2. Use of Demographic / Profile Data / Your Information
            </strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            We use your personal information to provide the product and services
            you request. To the extent we use your personal information to
            market to you, we will provide you the ability to opt-out of such
            uses. We use your personal information to assist sellers and
            business partners in handling and fulfilling orders; enhancing
            customer experience; resolve disputes; troubleshoot problems; help
            promote a safe service; collect money; measure consumer interest in
            our products and services; inform you about online and offline
            offers, products, services, and updates; customize and enhance your
            experience; detect and protect us against error, fraud and other
            criminal activity; enforce our terms and conditions; and as
            otherwise described to you at the time of collection of information.
          </Typography>
          <Typography variant="body1" className="p-10">
            With your consent, we will have access to your SMS, contacts in your
            directory, location and device information. We may also request you
            to provide your PAN, GST Number, Government issued ID cards/number
            and Know-Your-Customer (KYC) details to: (i) check your eligibility
            for certain products and services including but not limited to
            credit and payment products; (ii) issue GST invoice for the products
            and services purchased for your business requirements; (iii) enhance
            your experience on the Platform and provide you access to the
            products and services being offered by us, sellers, affiliates or
            lending partners. You understand that your access to these
            products/services may be affected in the event consent is not
            provided to us.
          </Typography>
          <Typography variant="body1" className="p-10">
            In our efforts to continually improve our product and service
            offerings, we and our affiliates collect and analyse demographic and
            profile data about our users' activity on our Platform. We identify
            and use your IP address to help diagnose problems with our server,
            and to administer our Platform. Your IP address is also used to help
            identify you and to gather broad demographic information.
          </Typography>
          <Typography variant="body1" className="p-10">
            We will occasionally ask you to participate in optional surveys
            conducted either by us or through a third party market research
            agency. These surveys may ask you for personal information, contact
            information, date of birth, demographic information (like zip code,
            age, or income level), attributes such as your interests, household
            or lifestyle information, your purchasing behaviour or history,
            preferences, and other such information that you may choose to
            provide. The surveys may involve collection of voice data or video
            recordings, the participation of which would purely be voluntary in
            nature. We use this data to tailor your experience at our Platform,
            providing you with content that we think you might be interested in
            and to display content according to your preferences.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>3. Cookies</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            We use data collection devices such as "cookies" on certain pages of
            the Platform to help analyse our web page flow, measure promotional
            effectiveness, and promote trust and safety. "Cookies" are small
            files placed on your hard drive that assist us in providing our
            services. Cookies do not contain any of your personal information.
            We offer certain features that are only available through the use of
            a "cookie". We also use cookies to allow you to enter your password
            less frequently during a session. Cookies can also help us provide
            information that is targeted to your interests. Most cookies are
            "session cookies," meaning that they are automatically deleted from
            your hard drive at the end of a session. You are always free to
            decline/delete our cookies if your browser permits, although in that
            case you may not be able to use certain features on the Platform and
            you may be required to re-enter your password more frequently during
            a session. Additionally, you may encounter "cookies" or other
            similar devices on certain pages of the Platform that are placed by
            third parties. We do not control the use of cookies by third
            parties. We use cookies from third-party partners such as Google
            Analytics for marketing and analytical purposes. Google Analytics
            help us understand how our customers use the site. You can read more
            about how Google uses your Personal Information here:{" "}
            <a href="https://www.google.com/intl/en/policies/privacy/">
              https://www.google.com/intl/en/policies/privacy/
            </a>
            . You can also opt-out of Google Analytics here:{" "}
            <a href="https://tools.google.com/dlpage/gaoptout">
              https://tools.google.com/dlpage/gaoptout
            </a>
            .
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>4. Sharing of personal information</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            We may share personal information with our other corporate entities
            and affiliates for purposes of providing products and services
            offered by them, such as, the deferred payment options EGadgets
            PayLater offered by EGadgets Advanz Private Limited through its
            lending partners. These entities and affiliates may share such
            information with their affiliates, business partners and other third
            parties for the purpose of providing you their products and
            services, and may market to you as a result of such sharing unless
            you explicitly opt-out.
          </Typography>
          <Typography variant="body1" className="p-10">
            We may disclose your personal information to third parties, such as
            sellers, business partners. This disclosure may be required for us
            to provide you access to our products and services; for fulfilment
            of your orders; for enhancing your experience; for providing
            feedback on products; to collect payments from you; to comply with
            our legal obligations; to conduct market research or surveys; to
            enforce our Terms of Use; to facilitate our marketing and
            advertising activities; to analyse data; for customer service
            assistance; to prevent, detect, mitigate, and investigate fraudulent
            or illegal activities related to our product and services. We do not
            disclose your personal information to third parties for their
            marketing and advertising purposes without your explicit consent.
          </Typography>
          <Typography variant="body1" className="p-10">
            We may disclose personal information if required to do so by law or
            in the good faith belief that such disclosure is reasonably
            necessary to respond to subpoenas, court orders, or other legal
            process. We may disclose personal information to law enforcement
            agencies, third party rights owners, or others in the good faith
            belief that such disclosure is reasonably necessary to: enforce our
            Terms of Use or Privacy Policy; respond to claims that an
            advertisement, posting or other content violates the rights of a
            third party; or protect the rights, property or personal safety of
            our users or the general public.
          </Typography>
          <Typography variant="body1" className="p-10">
            We and our affiliates will share / sell some or all of your personal
            information with another business entity should we (or our assets)
            plan to merge with, or be acquired by that business entity, or
            re-organization, amalgamation, restructuring of business. Should
            such a transaction occur that other business entity (or the new
            combined entity) will be required to follow this Privacy Policy with
            respect to your personal information.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>5. Links to Other Sites</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            Our Platform may provide links to other websites or application that
            may collect personal information about you. We are not responsible
            for the privacy practices or the content of those linked websites.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>6. Security Precautions</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            We maintain reasonable physical, electronic and procedural
            safeguards to protect your information. Whenever you access your
            account information, we offer the use of a secure server. Once your
            information is in our possession we adhere to our security
            guidelines to protect it against unauthorized access. However, by
            using the Platform, the users accept the inherent security
            implications of data transmission over the internet and the World
            Wide Web which cannot always be guaranteed as completely secure, and
            therefore, there would always remain certain inherent risks
            regarding use of the Platform. Users are responsible for ensuring
            the protection of login and password records for their account.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>7. Choice/Opt-Out</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            We provide all users with the opportunity to opt-out of receiving
            non-essential (promotional, marketing-related) communications after
            setting up an account with us. If you do not wish to receive
            promotional communications from us then please login into the
            Notification Preference page of Platform [
            <a href="https://www.EGadgets.com/communication-preferences/email">
              https://www.EGadgets.com/communication-preferences/email
            </a>
            ] to unsubscribe/opt-out.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>8. Advertisements on Platform</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            We use third-party advertising companies to serve ads when you visit
            our Platform. These companies may use information (not including
            your name, address, email address, or telephone number) about your
            visits to this and other websites in order to provide advertisements
            about goods and services of interest to you.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>9. Children Information</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            We do not knowingly solicit or collect personal information from
            children under the age of 18 and use of our Platform is available
            only to persons who can form a legally binding contract under the
            Indian Contract Act, 1872. If you are under the age of 18 years then
            you must use the Platform, application or services under the
            supervision of your parent, legal guardian, or any responsible
            adult.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>10. Data Retention</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            We retain your personal information in accordance with appliable
            laws, for a period no longer than is required for the purpose for
            which it was collected or as required under any applicable law.
            However, we may retain data related to you if we believe it may be
            necessary to prevent fraud or future abuse or if required by law or
            for other legitimate purposes. We may continue to retain your data
            in anonymised form for analytical and research purposes.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>11. Your Consent</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            By visiting our Platform or by providing your information, you
            consent to the collection, use, storage, disclosure and otherwise
            processing of your information (including sensitive personal
            information) on the Platform in accordance with this Privacy Policy.
            If you disclose to us any personal information relating to other
            people, you represent that you have the authority to do so and to
            permit us to use the information in accordance with this Privacy
            Policy.
          </Typography>
          <Typography variant="body1" className="p-10">
            You, while providing your personal information over the Platform or
            any partner platforms or establishments, consent to us (including
            our other corporate entities, affiliates, lending partners,
            technology partners, marketing channels, business partners and other
            third parties) to contact you through SMS, instant messaging apps,
            call and/or e-mail for the purposes specified in this Privacy
            Policy.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>12. Changes to this Privacy Policy</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            Please check our Privacy Policy periodically for changes. We may
            update this Privacy Policy to reflect changes to our information
            practices. We will alert you to significant changes by posting the
            date our policy got last updated, placing a notice on our Platform,
            or by sending you an email when we are required to do so by
            applicable law.
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>13. Grievance Officer</strong>
          </Typography>
          <Typography variant="body1" className="p-10">
            In accordance with Information Technology Act 2000 and rules made
            there under, the name and contact details of the Grievance Officer
            are provided below:
          </Typography>
          <Typography variant="body1" className="p-10">
            Mr. Shremanth M
          </Typography>
          <Typography variant="body1" className="p-10">
            EGadgets Internet Pvt Ltd.
          </Typography>
          <Typography variant="body1" className="p-10">
            Embassy tech village
          </Typography>
          <Typography variant="body1" className="p-10">
            8th floor Block 'B' Devarabeesanahalli Village,
          </Typography>
          <Typography variant="body1" className="p-10">
            Varthur Hobli, Bengaluru East Taluk,
          </Typography>
          <Typography variant="body1" className="p-10">
            Bengaluru District,
          </Typography>
          <Typography variant="body1" className="p-10">
            Karnataka, India, 560103.
          </Typography>
          <Typography variant="body1" className="p-10">
            Phone: 1800 202 9898
          </Typography>
          <Typography variant="body1" className="p-10">
            Email:{" "}
            <a href="mailto:grievance.officer@EGadgets.com">
              grievance.officer@EGadgets.com
            </a>
          </Typography>
          <Typography variant="body1" className="p-10">
            Time: Mon - Sat (9:00 - 18:00)
          </Typography>
          <Typography variant="body1" className="p-10">
            Customer Support: You can reach our customer support team to address
            any of your queries or complaints by clicking the link, selecting
            your order and choosing 'Need Help' option:{" "}
            <a href="https://www.EGadgets.com/helpcentre">
              https://www.EGadgets.com/helpcentre
            </a>
          </Typography>
          <Typography variant="body1" className="p-10">
            <strong>14. Queries</strong>{" "}
          </Typography>
          <Typography variant="body1" className="p-10">
            If you have a query, issue, concern, or complaint in relation to
            collection or usage of your personal information under this Privacy
            Policy, please contact us{" "}
            <a href="mailto:membercare@payback.in">at</a> the contact
            information provided above.
          </Typography>
        </div>
      </Container>
    );
  }
}

export default PrivacyPolicy;
